* {
	margin: 0;
	padding: 0;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Domus";
}

.centered {
	text-align: center;
}

.wrapper {
	width: 100%;
	padding: 30px 0;
}

.container {
	width: 1200px;
	margin: 0 auto;
}

.linked-course {
	border-color: #0034a0;
}

ul.rig {
	list-style: none;
	font-size: 0px;
	margin-left: -2.5%; /* should match li left margin */
}

ul.rig li {
	display: inline-block;
	padding: 10px;
	margin: 0 0 2.5% 2.5%;
	background: #fff;
	border: 1px solid #ddd;
	font-size: 16px;
	font-size: 1rem;
	vertical-align: top;
	-webkit-box-shadow: 0 0 5px #ddd;
	        box-shadow: 0 0 5px #ddd;
	box-sizing: border-box;
	-moz-box-sizing: border-box;
	-webkit-box-sizing: border-box;
}

ul.rig li img {
	max-width: 100%;
	height: auto;
	margin: 0 0 10px;
}

ul.rig li h3 {
	margin: 0 0 5px;
}

ul.rig li p {
	font-size: .9em;
	line-height: 1.5em;
	color: #0d2b5c;
}

ul.rig.columns-3 li {
	width: 30.83%;
}

.course-details {
  line-height: 43px;
  height: 43px;
  text-align: center;
}

.course-details h3 {
  vertical-align: middle;
  display: inline-block;
  line-height: 22px;
  color: #0034a0;
}

.single-course-1 {
  background-image: url('https://www.skhole.fi/img/course-1.jpg');
}
.single-course-2 {
  background-image: url('https://www.skhole.fi/img/course-2.jpg');
}
.single-course-3 {
  background-image: url('https://www.skhole.fi/img/course-3.jpg');
}
.single-course-4 {
  background-image: url('https://www.skhole.fi/img/course-4.jpg');
}
.single-course-5 {
  background-image: url('https://www.skhole.fi/img/course-5.jpg');
}
.single-course-6 {
  background-image: url('https://www.skhole.fi/img/course-6.jpg');
}

@media (max-width: 1199px) {
	.container {
		width: auto;
		padding: 0 10px;
	}
}

@media (max-width:640px) {
  .course-details h3 {
    line-height: 16px;
    font-size: 16px;
  }
}

@media (max-width: 480px) {
	ul.rig {
		margin-left: 0;
	}
	ul.rig li {
		width: 100% !important; /* over-ride all li styles */
		margin: 0 0 20px;
	}
}



